/***** 容器属性* 内容排列方向  flex-direction属性决定主轴的方向（即项目的排列方向）。* row（默认值）：主轴为水平方向，起点在左端。* row-reverse：主轴为水平方向，起点在右端。* column：主轴为垂直方向，起点在上沿。* column-reverse：主轴为垂直方向，起点在下沿。*****/
/*** 容器属性* 布局元素一排不下时，如何换行* @params nowrap 不换行* @params wrap 换行，第一行在上方* @params wrap-reverse  换行，第一行在下方*/
/**** flex-flow* 容器属性  是flex-direction属性和flex-wrap属性的简写形式，默认值为row nowrap。**/
/*** align-items* 容器属性  元素的 垂直对齐* @flex-start     元素的顶端对齐 并靠元素的顶端分布* @flex-end       元素的底部对齐 并靠容器的底部分布* @center         容器中垂直居中，并每个元素的中心点对齐* @baseline       项目的第一行文字的基线对齐。* @stretch        （默认值）：如果项目未设置高度或设为auto，将占满整个容器的高度。*/
/*** justify-content* 容器属性  元素的 水平对齐和分布方式* @flex-start     左对齐* @flex-end       右对齐* @center         居中* @space-between  两端对齐，项目之间的间隔都相等，与边框没有间距* @space-around   每个项目两侧的间隔相等。所以，项目之间的间隔比项目与边框的间隔大一倍。*/
/*** align-content* 容器属性* 属性定义了多根轴线的对齐方式。项目达到多行时的分布方式，如果项目只有一行，该属性不起作用。* @params flex-start 靠容器项端分布* @params flex-end 靠容器底部分布* @params center  垂直居中分布* @params space-between  垂直平均分布* @params space-around  每行元素的上下间距相等，因此每行之间的间隔要 比首行和未行 与边框的间隔大一倍* @params stretch  垂直拉伸占满整个容器*/
/*** 子元素的属性 Flex, 有三个属性，是 flex-grow, flex-shrink 和 flex-basis的简写方式** @$fg=flex-grow* 属性定义项目的放大比例，默认为0，即如果存在剩余空间，也不放大，属性都为1，则它们将等分剩余空间** @$fs=flex-shrink* 属性定义了项目的缩小比例，默认为1，即如果空间不足，该项目将缩小。* flex-shrink属性都为1，当空间不足时，都将等比例缩小。如果一个项目的flex-shrink属性为0，其他项目都为1，则空间不足时，前者不缩小。* @$fb =  flex-basis* 设为跟width或height属性一样的值（比如350px），则项目将占据固定空间。*它的默认值为auto，即项目的本来大小*/
/**** align-self* 子元素的属性* 属性允许单个项目有与其他项目不一样的对齐方式，可覆盖align-items属性。* 默认值为auto，表示继承父元素的align-items属性* 属性可能取6个值，除了auto，其他都与align-items属性完全一致**/
/**** order* 子元素的属性* 属性定义项目的排列顺序。数值越小，排列越靠前，默认为0。**/
